body{
  /* background-color: transparent; */
   overflow-x: hidden;
   font-family: 'ArialRounded', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
 }
 .main{
   padding-left: 25%;
   padding-right: 25%;
   height: 100vh;
   width: 100vw;
 
 }
 /*Heder component css start here*/
 .header{
   height: 70px;
   padding: 10px 0px;
   border-bottom: 1px solid;
 }
 .header-text{
     display: inline-block;
     color: white;
 /*    margin: 0px;*/
     padding: 10px 0px;
     font-size: 20px;
 }
 .header-button{
   float: right;
   padding: 10px;
   display: space-between;
 }
 .header-button button{
     border-radius: 4px;
     cursor: pointer;
     opacity: 0.9;
     background: none rgba(255, 255, 255, 0.2);
     box-shadow: none;
     margin-left:0px;
     margin-right: 20px;
     font-size: 13px;
     font-weight: 500;
     padding: 5px 10px;
     min-width: 70px;
     border: none;
     color: white !important;
 }
 /*Home component css start here*/
 .home_component{
     margin-top: 40px;
 }
 .first_row{
   margin-left: 10%;
   margin-right: 10%;
 }
 .second_row{
   height: 30px;
   height: 300px;
   border-radius: 8px;
   background-color: #ffffff24;
 }
 
 .second_row #pomodoro{
     border: none;
     margin: 0px;
     border-radius: 4px;
     font-size: 16px;
     padding: 2px 12px;
     height: 28px;
     cursor: pointer;
     background: none;
     box-shadow: none;
     color: white;
     opacity: 1;
     font-weight: bold;
 }
 .second_row #shortBreack{
     border: none;
     color: white;
     margin: 0px;
     border-radius: 4px;
     font-size: 16px;
     padding: 2px 12px;
     height: 28px;
     cursor: pointer;
     background: none;
     box-shadow: none;
     font-weight: bold;
 }
 .second_row #longBreack{
   border: none;
     color: white;
     margin: 0px;
     border-radius: 4px;
     font-size: 16px;
     padding: 2px 12px;
     height: 28px;
     cursor: pointer;
     background: none;
     box-shadow: none;
     font-weight: bold;
 }
 .button_col{
       display: inline-flex;
     -webkit-box-pack: center;
     justify-content: center;
     -webkit-box-align: center;
     align-items: center;
 }
 
 /*Timer component css start here*/
 .stopwatch-time {
     text-align: center;
     color: #fff;
     font-size: 100px;
 }
 .startBut{
     cursor: pointer;
     border: none;
     margin: 20px 0px 0px;
     padding: 0px 12px;
     border-radius: 4px;
     box-shadow: rgb(235, 235, 235) 0px 6px 0px;
     font-size: 22px;
     height: 55px;
     color: rgb(186, 73, 73);
     font-weight: bold;
     width: 200px;
     background-color: white;
     transition: color 0.5s ease-in-out 0s;
 }
 .pomodoro_count{
   color: whitesmoke;
   font-size: 16px;
   width: 20px;
   cursor: pointer;
   user-select: none;
   margin: auto;
   margin-top: 3%;
 }
 .taskName{
   font-size: 20px;
   color: white;
   font-weight: 400;
 }
 /*Task component ends here*/
 .task_header{
   color: white;
   width: 100%;
   font-weight: 500;
   font-size: 20px;
   padding-bottom: 10px;
   border-bottom: 2px solid white;
 }
 .dots_menu{
   float: right;
   cursor: pointer;
   user-select: none;
 }
 .task_button{
    box-sizing: border-box;
     width: 100%;
     height: 64px;
     background-color: rgba(0, 0, 0, 0.1);
     border-radius: 8px;
     display: flex;
     -webkit-box-pack: center;
     justify-content: center;
     -webkit-box-align: center;
     align-items: center;
     cursor: pointer;
     opacity: 0.8;
     color: white;
     margin-top: 12px;
     border: 2px dashed rgba(255, 255, 255, 0.4);
 }
 .task_add_box{
   padding: 10px 20px 0px;
   height: 280px;
   background-color: white;
   margin: 20px 0px 20px 0px;
   border-radius: 10px;
 }
 .task_add_box .task_name_input{
     border-radius: 4px;
     font-size: 22px;
     padding: 10px 0px;
     box-shadow: none;
     border: none;
     width: 100%;
     border-bottom: 1px solid black;
     box-sizing: border-box;
     font-weight: 500;
 }
 .task_add_box input:active{
   border: none;
 }
 .task_add_box span{
   margin-top: 30px;
   color: rgb(85, 85, 85);
   font-weight: bold;
 }
 .number_input{
   border-radius: 4px;
   background-color: rgb(239, 239, 239);
   font-size: 16px;
   padding: 10px;
   box-shadow: none;
   border: none;
   width: 100%;
   color: rgb(85, 85, 85);
   box-sizing: border-box;
   font-weight: bold;
   margin-top: 10px;
 }
 .btn_div{
   margin-top: 80px;
   float: right;
 /*  width: 100%;*/
   background-color: rgb(239, 239, 239);
 }
 .cancel_btn{
  text-align: center;
     border-radius: 7px;
     cursor: pointer;
     opacity: 0.9;
     font-size: 14px;
     padding: 8px 8px;
     min-width: 70px;
     display: inline-block;
     margin-right: 14px;
     background: none;
     border: none;
     color: rgb(136, 136, 136);
     font-weight: bold;
     box-shadow: none;
 }
 .save_btn{
       text-align: center;
     border-radius: 4px;
     cursor: pointer;
     box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 2px;
     color: white;
     opacity: 0.9;
     font-size: 14px;
     padding: 8px 12px;
     min-width: 70px;
     background-color: rgb(34, 34, 34);
     border: 2px solid rgb(34, 34, 34);
     display: inline-block;
 }
 .task_preview{
     -webkit-box-align: center;
     -webkit-box-pack: justify;
     display: flex;
     align-items: center;
     width: 100%;
     background-color: #dedede;
     padding: 13px;
     font-size: x-large;
     margin-top: 2%;
     border-radius: 10px;
     font-weight: 400;
     text-transform: capitalize;
 } 
  .task_preview .delete_task{
   float: right;
   justify-content: end;
   cursor: pointer;
 }
 #taskName{
   width: 100%;
 }
 .checkTask{
   margin-right: 2%;
   color:grey;
 }
 /* Stting div css start here */
 .setting_div{
  width: 80%;
  height: 380px;
  background-color: rgba(255, 255, 255, 0.76);
  margin-top: 2%;
  padding: 5%;
  margin-left: 10%;
  border-radius: 3%; 
 }
 .timeName {
  display: inline-block;
}
.time_input_div {
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  margin-top: 10px;
}
.input_div {
  width: 98px;
}
.lable_name {
  display: block;
  font-size: 14px;
  color: rgb(140, 135, 135);
  font-weight: bold;
  margin-bottom: 4px;
}

.number_input {
  border-radius: 4px;
  background-color: rgb(239, 239, 239);
  font-size: 16px;
  padding: 10px;
  box-shadow: none;
  border: none;
  color: rgb(85, 85, 85);
  width: 100%;
  box-sizing: border-box;
}
.setting_done_btn {
  padding: 14px 20px;
  text-align: right;
  margin-top: 30%;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.done_btn {
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  text-align: center;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 2px;
  color: white;
  opacity: 0.9;
  font-size: 14px;
  padding: 8px 12px;
  min-width: 70px;
  background-color: rgb(34, 34, 34);
  border: 2px solid rgb(34, 34, 34);
  display: inline-block;
}